import React, {useState, useEffect, useRef} from 'react'
import "./_selected.scss"

export default function LinksDropdown( props ) {

    const {
        day,
        month,
        year,
        types,
        names,
        Id,
        wids,
        data=[],
        now,
        widths,
        monthSelect = Function.prototype
    } = props

    const Select = useRef();
    const SelectText = useRef();
    const [isOpenSelect, setIsOpenSelect] = useState(false)



useEffect(()=>{
    SelectText.current.innerText = Select.current.options[Select.current.value].text;
}, [Select.value, isOpenSelect,now])

    return (
        <div
        tabindex="-1"
        onBlur={(e)=> {setIsOpenSelect(false)}}
        style={{width: widths ? ({widths, marginLeft: "0"}) : "15rem"}}
        onClick={(e)=> {setIsOpenSelect(!isOpenSelect)}}
         className={isOpenSelect ? " selectric-wrapper selectric-js-select-time selectric-belowselectric-open selectric-focus " : "selectric-wrapper selectric-js-select-time selectric-below"} >
            <div className="selectric-hide-select" >
                <select
                    ref={Select}
                    className="js-select-time"
                    name="ShippingCalculateForm[time]"
                     >
                    <option value="0" >{now ? now : 'Выбрать'}</option>
                   {data.map((item, index) =>(<>
                    <option value={item.id}>{item.data}</option>
                  </> ))}
                </select>
            </div>
            <div 
            className="selectric">
                <span ref={SelectText} className="label" ></span>
                <b className="button">▾</b >
            </div>
            
            {  isOpenSelect && <div 
            className="selectric-items" 
            style={{display: "block",  zIndex: "29", width: "100%"}} >
                <div className="selectric-scroll">
                    <ul>
                        <li
                         className="highlighted" 
                        data-index="0" 
                        onClick={(e)=> {setIsOpenSelect(!isOpenSelect)
                            Select.current.options[e.target.dataset.index].selected = true }}
                        >
                            {now ? now : 'Выбрать'}</li >
                        {data.map((item,index) => (<>
                        < li 
                            data-index={item.id}
                            onClick={(e)=> {setIsOpenSelect(!isOpenSelect);
                            Select.current.options[e.target.dataset.index].selected = true ;
                            monthSelect(e.target.dataset.index)}}>{item.data}</li>
                         </>) )} 
                    </ul>
                </div>
            </div>
}
            <input className="selectric-input" tabindex="0" />
        </div>
    )
}