import React, {useState, useEffect} from 'react'
import "./_midle.scss"
import LinksDropdown from "../Dropdown/selected"
import Goods from "../Goods/goods.jsx"



export default function Midle() {


    const toDay = new Date()
    const [ days, setdays ] = useState( toDay.getDate() + 1 )
    const [ months, setmonths ] = useState( toDay.getMonth() + 1 )
    const [ monthString, setMonthString ] = useState()
    const [ years, setyears ] = useState( toDay.getFullYear() )
    const [ maxday, setMaxday ] = useState( 32 - new Date( years, months, 32 ).getDate() )
    const [ dayAll, setDayAll ] = useState( [] )
    let count = []
    const [ goods, setGoods ] = useState( [ {id: 0, quantity: 1} ] )
    const monthAll = [
        {id: 1, data: "Январь"},
        {id: 2, data: "Февраль"},
        {id: 3, data: "Март"},
        {id: 4, data: "Апрель"},
        {id: 5, data: "Май"},
        {id: 6, data: "Июнь"},
        {id: 7, data: "Июль"},
        {id: 8, data: "Август"},
        {id: 9, data: "Сентябрь"},
        {id: 10, data: "Октябрь"},
        {id: 11, data: "Ноябрь"},
        {id: 12, data: "Декабрь"} ]
    const yearsAll = [
        {id: 1, data: years},
        {id: 2, data: years + 1},
        {id: 3, data: years + 2} ]
    const workshop = [
        {id: 1, data: "Кухня ресторана №1"},
        {id: 2, data: "Кухня ресторана №2"},
        {id: 3, data: "Кухня ресторана №3"}
    ]
    const name = [
        {id: 1, data: "Андрей"},
        {id: 2, data: "Анатолий"},
        {id: 3, data: "Станислав"}
    ]

    const monthSelect = ( dat ) => {
        let mon = new Date( years, dat - 1, 32 ).getDate()
        setMaxday( 32 - mon )
        console.log( dat, maxday, mon )
    }

    useEffect( () => {
        setMonthString( () => {
            for( let i = 1;i <= monthAll.length;i++ ) {
                if( monthAll[ i ].id === months ) {return monthAll[ i ].data}
            }
        } )
    }, [ monthAll, months, years ] )

    useEffect( () => {
        for( let i = 1;i <= maxday;i++ ) {
            count[ i ] = {"id": i, "data": i}
        }
        setDayAll( count )
        // console.log(dayAll)
    }, [ maxday ] )

    const addGoodsCount = ( id, q ) => {
        const newGoodsItem = goods.map( item => {
            if( item.id === id ) {
                if( q ) {
                    return {
                        ...item,
                        quantity: q
                    }
                } else {
                    return {
                        ...item,
                        quantity: item.quantity + 1
                    }
                }
            } else {
                return item
            }
        } )
        setGoods( newGoodsItem )
    }
    const removeGoodsCount = ( id, q ) => {
        if( q > 1 ) {
            const newGoodsItem = goods.map( item => {
                if( item.id === id && item.quantity >= 2 ) {
                    return {
                        ...item,
                        quantity: item.quantity - 1
                    }
                } else {
                    return item
                }
            } )
            setGoods( newGoodsItem )
        }
        if( q === 1 ) {
            delCart( id )
        }
    }

    const delCart = ( id ) => {
        if( goods.length > 1 ) {
            const newGoods = goods.filter( el => el.id !== id )
            setGoods( newGoods )
        }
    }
    const AddGoods = () => {
        let newGoods = []
        for( let i = 0;i < goods.length;i++ ) {
            newGoods[ i ] = {id: goods[ i ].id, quantity: goods[ i ].quantity}
        }
        newGoods[ goods.length ] = {id: goods.length, quantity: 1}
        setTimeout( () => {setGoods( newGoods ); console.log( newGoods )}, 100 )
    }

    return (
        <>
            <div className="order__content">
                <div className="label--left">
                    Данные заказщика
                </div>
                <div style={{marginBottom: "5vw"}}>
                    <div className="label--sm"> Заказывайте заранее. Составляйте план закупок за 2-3 дня и заказывайте сразу на несколько дней вперёд. Не гоняйте водителя каждый день за очередным пучком зелени или кисточкой для пупка. Понятно, что бывают экстремальные ситуации, требующие срочных покупок, но не злоупотребляйте этим.
                    </div>
                    <div className="label--mar">
                        Ваш цех
                    </div>
                    <LinksDropdown data={workshop} />
                    <div className="label--mar">
                        Ваше имя
                    </div>
                    <LinksDropdown data={name} />
                    <small className="label--sm label--mar">
                        ВЫБЕРИТЕ ДАТУ ПОСТАВКИ
                    </small>
                    <div className="order__date">
                        <LinksDropdown
                            data={dayAll}
                            now={days}
                            widths="4rem"
                            wids="6.5rem" />
                        <LinksDropdown
                            monthSelect={monthSelect}
                            data={monthAll}
                            now={monthString}
                            widths="7rem"
                            wids="8.2rem" />
                        <LinksDropdown
                            data={yearsAll}
                            now={years}
                            widths="5.125rem"
                            wids="7.625rem" />
                    </div>
                </div>
                <div className="label--left">
                    Состав заказа
                </div>
                <div className="form__order__info">
                    <small
                        className="label--sm">
                        ДАННЫЕ О ТОВАРАХ
                    </small>
                    <div>
                    {
                        goods.length >= 1 &&
                        goods.map( ( item ) =>
                        <Goods
                        key={"g" + item.id}
                        quantity={item.quantity}
                        workshop={workshop}
                        addGoodsCount={addGoodsCount}
                        id={item.id}
                        removeGoodsCount={removeGoodsCount} />
                        )
                    }
                    </div>
                    <div
                        className="btn btn-secondary"
                        onClick={() => AddGoods()}>
                        <span>
                            <svg class="icon icon-plus">
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    xlink="http://www.w3.org/1999/xlink"
                                    viewBox="0 0 14 14"
                                    fontWeight="500"
                                    width="14px" height="14px">
                                    <path fill-rule="evenodd" fill="#6ab04c"
                                        d="M13.000,8.000 L8.000,8.000 L8.000,13.000 C8.000,13.552 7.552,14.000 7.000,14.000 C6.448,14.000 6.000,13.552 6.000,13.000 L6.000,8.000 L1.000,8.000 C0.448,8.000 0.000,7.552 0.000,7.000 C0.000,6.448 0.448,6.000 1.000,6.000 L6.000,6.000 L6.000,1.000 C6.000,0.448 6.448,0.000 7.000,0.000 C7.552,0.000 8.000,0.448 8.000,1.000 L8.000,6.000 L13.000,6.000 C13.552,6.000 14.000,6.448 14.000,7.000 C14.000,7.552 13.552,8.000 13.000,8.000 Z" />
                                </svg>
                            </svg>
                        </span>
                        <span>
                            Добавить товар
                        </span>
                    </div>
                </div>
                <div className="label--left">
                    Дополнительные товары
                </div>
                <div >
                    <div
                        className="label--sm" >
                        В свободной форме укажите товары и количества не из списка в поле ниже
                    </div>
                    <div>
                        <label>
                            Какие еще товары и в каком количестве нужно приобрести:
                        </label>
                        <textarea className="textarea label--mar" />
                    </div>
                </div>
                <div
                    className="label--left">
                    Подтверждение
                </div>
                <div >
                    <div>
                        
                    <label>
                        Пожелание / комментарий ко всему заказу:
                    </label>
                    <textarea className="textarea" style={{marginTop: "1rem", marginRight: "2rem", width: "80%", height: "5vw"}} />
                    </div>
                    <div>
                        <button 
                        style={{marginBottom: "1rem"}}
                        class="btn btn-primary">
                                    <span>отправить заказ</span>
                        </button>
                    </div>
                </div>
            </div>
        </>
    )
}